@media (width >= 768px) {
  h1 {
    font-size: 3.6rem;
  }

  h2 {
    font-size: 1.8rem;
  }

  p, strong, a, span, button, input, label, select, option {
    font-size: 1.4rem;
  }

  #book-form div:last-child {
    justify-content: center;
    gap: 30px;
  }

  #book-form button {
    flex: unset;
    width: auto;
    min-width: 120px;
  }
}

@media (width >= 768px) and (width <= 1023px) {
  #book-list {
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    display: grid;
  }
}

@media (width >= 1024px) {
  #book-list {
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
    display: grid;
  }
}
